import Vue from 'vue'
import VeeValidate, { Validator } from 'vee-validate'
import router from './routers/index.js'
import store from './store/index.js'
import './directives/index.js'
import App from './App.vue'
import './api/index.js'
import bkMagicVue, { locale, lang } from 'bk-magic-vue'
import 'bk-magic-vue/dist/bk-magic-vue.min.css'
import { Input, Select, Radio, RadioGroup, RadioButton, Checkbox,
    CheckboxGroup, Button, Option, OptionGroup, Table, TableColumn,
    DatePicker, TimePicker, TimeSelect, Upload, Tree, Loading,
    Container, Row, Col, Pagination } from 'element-ui'
import enLocale from 'element-ui/lib/locale/lang/en'
import zhLocale from 'element-ui/lib/locale/lang/zh-CN'
import locales from 'element-ui/lib/locale'
import { STRING_LENGTH } from '@/constants/index.js'
const cron = require('@/assets/js/node-cron-valid/node-cron-vaild.js')
Vue.use(VeeValidate)

Vue.use(bkMagicVue)

Vue.use(Input)
Vue.use(Select)
Vue.use(Radio)
Vue.use(RadioGroup)
Vue.use(RadioButton)
Vue.use(Checkbox)
Vue.use(CheckboxGroup)
Vue.use(Button)
Vue.use(Option)
Vue.use(OptionGroup)
Vue.use(Table)
Vue.use(TableColumn)
Vue.use(DatePicker)
Vue.use(TimeSelect)
Vue.use(TimePicker)
Vue.use(Upload)
Vue.use(Tree)
Vue.use(Loading.directive)
Vue.use(Container)
Vue.use(Row)
Vue.use(Col)
Vue.use(Pagination)

if (store.state.lang === 'en') {
    locale.use(lang.enUS)
    locales.use(enLocale)
} else {
    locales.use(zhLocale)
}

$.atoms = {} // hack atom config load

const InvalidNameChar = '\'"$&<>'
Validator.extend('cronRlue', {
    getMessage: (field, args) => {
        return args + gettext('')
    },
    validate: value => cron.validate(value).status
})
Validator.extend('integer', {
    getMessage: (field, args) => {
        return args + gettext('')
    },
    validate: value => Number(value) >= 1 && Number(value) % 1 === 0
})
Validator.localize({
    en: {
        messages: {
            required: gettext('')
        },
        custom: {
            templateName: {
                required: gettext(''),
                regex: gettext('') + InvalidNameChar + gettext(''),
                max: gettext('') + STRING_LENGTH.TEMPLATE_NAME_MAX_LENGTH + gettext('')
            },
            nodeName: {
                required: gettext(''),
                regex: gettext('') + InvalidNameChar + gettext(''),
                max: gettext('') + STRING_LENGTH.TEMPLATE_NODE_NAME_MAX_LENGTH + gettext('')
            },
            stageName: {
                regex: gettext('') + InvalidNameChar + gettext(''),
                max: gettext('') + STRING_LENGTH.STAGE_NAME_MAX_LENGTH + gettext('')
            },
            variableName: {
                required: gettext(''),
                regex: gettext('') + InvalidNameChar + gettext(''),
                max: gettext('') + STRING_LENGTH.VARIABLE_NAME_MAX_LENGTH + gettext('')
            },
            variableKey: {
                required: gettext('KEY'),
                regex: gettext('KEY'),
                max: gettext('KEY') + STRING_LENGTH.VARIABLE_KEY_MAX_LENGTH + gettext(''),
                keyRepeat: gettext('KEY')
            },
            defaultValue: {
                required: gettext(''),
                customValueCheck: gettext('')
            },
            valueValidation: {
                validReg: gettext('')
            },
            taskName: {
                required: gettext(''),
                regex: gettext('') + InvalidNameChar + gettext(''),
                max: gettext('') + STRING_LENGTH.TASK_NAME_MAX_LENGTH + gettext('')
            },
            schemeName: {
                required: gettext(''),
                regex: gettext('') + InvalidNameChar + gettext(''),
                max: gettext('') + STRING_LENGTH.SCHEME_NAME_MAX_LENGTH + gettext('')
            },
            appTemplate: {
                required: gettext('')
            },
            appName: {
                required: gettext(''),
                regex: gettext('') + InvalidNameChar + gettext(''),
                max: gettext('') + STRING_LENGTH.APP_NAME_MAX_LENGTH + gettext('')
            },
            appDesc: {
                max: gettext('') + STRING_LENGTH.APP_DESCRIPTION_MAX_LENGTH + gettext('')
            },
            periodicName: {
                required: gettext(''),
                regex: gettext('') + InvalidNameChar + gettext(''),
                max: gettext('') + STRING_LENGTH.TEMPLATE_NAME_MAX_LENGTH + gettext('')
            },
            periodicCron: {
                required: gettext(''),
                regex: gettext('')
            },
            interval: {
                required: gettext(''),
                regex: gettext('')
            },
            draftName: {
                required: gettext(''),
                regex: gettext('') + InvalidNameChar + gettext(''),
                max: gettext('') + STRING_LENGTH.DRAFT_NAME_MAX_LENGTH + gettext('')
            },
            packageName: {
                regex: gettext(''),
                max: gettext('') + STRING_LENGTH.SOURCE_NAME_MAX_LENGTH + gettext('')
            },
            moduleName: {
                regex: gettext(''),
                max: gettext('') + STRING_LENGTH.SOURCE_NAME_MAX_LENGTH + gettext('')
            },
            cacheName: {
                regex: gettext(''),
                max: gettext('') + STRING_LENGTH.SOURCE_NAME_MAX_LENGTH + gettext('')
            },
            minRule: {
                required: gettext(''),
                regex: gettext(' 0 - 59 ')
            },
            hourRule: {
                required: gettext(''),
                regex: gettext(' 0 - 23 ')
            },
            weekRule: {
                required: gettext(''),
                regex: gettext(' 0 - 6 ')
            },
            dayRule: {
                required: gettext(''),
                regex: gettext(' 1 - 31 ')
            },
            monthRule: {
                required: gettext(''),
                regex: gettext(' 1 - 12 ')
            },
            testName: {
                required: gettext('test'),
                regex: gettext(' test ')
            }
        }
    }
})

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#root')
