import Vue from 'vue'

Vue.directive('clickout', {
    bind (el, binding) {
        function handler (e) {
            if (el.contains(e.target)) {
                return
            }
            if (binding.expression) {
                typeof binding.value === 'function' && binding.value()
            }
        }
        el.handler = handler
        window.addEventListener('click', el.handler)
    },
    unbind (el) {
        window.removeEventListener('click', el.handler)
    }
})
